.skills-container {
    display: flex;
    min-height: 35em;
    justify-content: center;
    flex-direction: column;
    background-color: #a9def9;
    margin-bottom: 8em;

}       

.skills-inner-one {
    align-self: center;
    margin: 2em;
    min-width: 60em;
    padding: 2em;
    display: flex;
    flex-direction: column;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;    
    border-radius: 1em;
    justify-content: space-around;
    background-color: white;


}
.skills-inner-one {

    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-property: transform;
    transition-property: transform;
  }
  .skills-inner-one:hover,    .skills-inner-one:focus,    .skills-inner-one:active {
    -webkit-transform: scale(1.04);
    transform: scale(1.04);
  }


.skills-holder-one {
    display: flex;
    align-items: center;
    justify-content: space-around;


}
.skill-content-container {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    min-width: 6em;
    max-width: 6em;
    max-height: 6em;
    min-height: 6em;
}

.skill-icon {

    font-size: 3em
}

.skill-name-container {

    font-size: 1em;
}



.page-break {
    position: relative;

}

.custom-shape-divider-bottom-1663029468 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.custom-shape-divider-bottom-1663029468 svg {
    position: relative;
    display: block;
    width: calc(135% + 1.3px);
    height: 116px;
}

.custom-shape-divider-bottom-1663029468 .shape-fill {
    fill: #A9DEF9;
}

.custom-shape-divider-top-1663029526 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-top-1663029526 svg {
    position: relative;
    display: block;
    width: calc(131% + 1.3px);
    height: 179px;
}

.custom-shape-divider-top-1663029526 .shape-fill {
    fill: #A9DEF9;
}


@media (max-width: 900px) {

    .skills-inner-one {
        min-width: 0em;
        max-width: 100%;
        align-self: center;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
    }
    .skills-container {
        justify-content: center;
        align-items: center;
        padding: 1em;
    }
    .custom-shape-divider-top-1663029526 {
        width: 120%;
    }
    .skills-holder-one {
        min-width: 20em;
    }
    .skill-content-container {
        max-width: 2em;
        max-height: 2em;
        font-size: 80%
    }
}

.helper {
    z-index:0;
}

.shape-fill {
    z-index: 0;
}